.card {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    width: 250px;
    height: 300px;
    padding: 15px;
    border: 1px solid #afafaf;
    border-radius: 5px;
    box-shadow: 2px 2px lightgray, 4px 4px darkgray, 6px 6px gray;
    transition: .3s;
    text-align: center;
}

.card:hover {
    transform: scale(1.03);
    transition: .3s;
}

.cardHeader{
    overflow: hidden;
    width: 100%;
    height: 100%;
    flex-basis: 10%;
    border-bottom: 1px solid gray;
}

.ratingCount{
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid gray;
}

.cardsCount, .rating {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.rating{
    border-left: 1px solid gray;
}

.cardsCount span, .rating span{
    font-size: 24px;
    font-weight: bold;
}

.actions{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-around;
}

.user{
    overflow: hidden;
}
.user span{
    text-align: center;
    font-size: 12px;
}

